import { IImagePath } from '../types';

export class ImgCacheService {
  private cache: Set<IImagePath> = new Set();

  isImageLoaded(imagePath: IImagePath): boolean {
    return this.cache.has(imagePath);
  }

  setImageLoaded(imagePath: IImagePath): void {
    this.cache.add(imagePath);
  }
}

export const imageCacheService = new ImgCacheService();
