import React from 'react';
import { imageStorage } from '../services';
import { imageCacheService } from '../services/img-cache.service';
import { IBase64ImagePlaceholder, IImagePath } from '../types';
import { fetchImagePromise } from '../utils';

type TUsePlaceholderImgResult = {
  pixelated: boolean;
  currentSrc: IImagePath | IBase64ImagePlaceholder;
};

export function usePlaceholderImg(imgSrc: IImagePath, isInView: boolean = true): TUsePlaceholderImgResult {
  const isImageAlreadyLoaded = React.useMemo(() => imageCacheService.isImageLoaded(imgSrc), [imgSrc]);

  const [pixelated, setPixelated] = React.useState(!isImageAlreadyLoaded);

  const [currentSrc, setCurrentSrc]
    = React.useState<IImagePath | IBase64ImagePlaceholder>((): IImagePath | IBase64ImagePlaceholder => {
      if (!isImageAlreadyLoaded) return imageStorage.getImageBase64Data(imgSrc);

      return imgSrc;
    });
  const [isMounted, setIsMounted] = React.useState(true);

  React.useEffect(() => () => setIsMounted(false), []);

  React.useEffect(() => {
    if (isMounted && isInView) {
      setPixelated(!isImageAlreadyLoaded);
      setCurrentSrc((): IImagePath | IBase64ImagePlaceholder => {
        if (!isImageAlreadyLoaded) return imageStorage.getImageBase64Data(imgSrc);

        return imgSrc;
      });

      fetchImagePromise(imgSrc).finally(() => {
        setPixelated(false);
        setCurrentSrc(imgSrc);
      });
    }
  }, [imgSrc, isMounted, isInView, isImageAlreadyLoaded]);

  return { pixelated, currentSrc };
}
