import { fromEvent } from 'rxjs';
import type { FingerPrintService } from './fingerprint.service';
import type { UserDecisionStorageService } from './user-decision-storage.service';
import { UserRequestTypeType } from '../enums';
import type { SyndicateTransport } from '../transport';
import { generateUrl } from '../utils';

export class OnleaveResultSendService {
  private fingerPrint: string = '';

  constructor(
    private transport: SyndicateTransport,
    private storage: UserDecisionStorageService,
    fingerPrintService: FingerPrintService
  ) {
    void fingerPrintService.getFingerPrint().then((fingerPrint: string) => {
      this.fingerPrint = fingerPrint;
    });
  }

  init(): void {
    fromEvent(window, 'unload').subscribe(this.sendData);
  }

  sendData = (): void => {
    const result = this.storage.getItems();

    if (result.length) {
      this.transport.setUserSelectionOnleave({
        fingerPrint: this.fingerPrint,
        name: '',
        contact: '',
        result,
        url: generateUrl(),
        type: UserRequestTypeType.ONLEAVE_PAGE
      });
    }
  };
}
