import React from 'react';
import { BurgerIcon } from './burger-icon';
import { buttonStyle } from './menu-button.style';
import { Text } from '../../text';

type TMenuButtonProps = {
  onClick: VoidFunction;
  isOpened: boolean;
};

export function MenuButton({ onClick, isOpened }: TMenuButtonProps): JSX.Element {
  const [isHovered, setIsHovered] = React.useState(false);

  const handleSetHovered = React.useCallback(() => setIsHovered((prevVal: boolean) => !prevVal), []);

  return (
    <div
      className={buttonStyle}
      onMouseEnter={handleSetHovered}
      onMouseLeave={handleSetHovered}
      onTouchStart={handleSetHovered}
      onTouchEnd={handleSetHovered}
    >
      <Text
        onClick={onClick}
        color="black"
        align="center"
      >
        <BurgerIcon isOpened={isOpened} isHovered={isHovered} />
        &nbsp;
        {isOpened ? 'close menu' : 'menu'}
      </Text>
    </div>
  );
}
